import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { Message, ConfirmationService } from "primeng/primeng";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { ActivatedRoute, Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
import { InterestRateMaintenance, InterestDefineDetail } from "app/pages/tzb/valuation/interest-rate-manage/deposit-interest-rate/interest-rate-maintenance/bean/interest-rate-maintenance.bean";
import { checkChinese, TwoInteger } from 'app/@uisftech/common/service/validator';
import * as _ from 'lodash';
@Component({
    selector: 'interest-define-detail',
    templateUrl: './interest-define-detail.component.html',
    styleUrls: ['./interest-define-detail.component.scss'],
    providers: [ConfirmationService, Commfunc]
})
export class InterestDefineDetailComponent implements OnInit {
    code: any;
    ch: any = CALENDAR_CN;
    tabMesg: any = tableMessage;
    msgs: Message[] = [];
    first: number = 0;
    pageSize: number = 10;
    pageNum: number = 1;
    total: number = 0;
    display: Boolean = false;//新增、修改弹框控制
    orgTreeDisplay: Boolean = false;//新增、修改弹框内机构树弹框控制
    orgTreeDisplay1: Boolean = false;//机构树弹框控制
    showTree: boolean = true;//控制机构树显示
    numberShow: boolean = false;//新增、修改弹框利率值校验
    numberShow1: boolean = false;//利率值校验
    timeShow: boolean = false;//生效日期校验
    timeShow1: boolean = false;//失效日期校验
    rateValueCodenull: boolean = false;//利率值必输校验
    fromDatenull: boolean = false;//生效日期必输校验
    OrgNamenull: boolean = false;//机构片区必输校验
    isClick: boolean = false;//提交按钮不可连续多次点击控制
    iswork: boolean = false;//是否是工作流
    list_Table: any = [];//查询数据
    fromDateBegin: Date = null;//	开始日期起
    fromDateEnd: Date = null;//开始日期止
    thruDateBegin: Date = null;//	结束日期起
    thruDateEnd: Date = null;//	结束日期止
    minDate: Date = new Date();//	当前日期
    nextDate: Date;//	当前日期后一天
    data: any;//父页面传递的数据
    title: any;//标题
    listOptions: any = [];//利率类别码值
    interestform: any = [];//利率类型码值
    interestType: any = [];//利率种类码值
    state: any = [];//状态码值
    ratePurposeList: any = [];//修改批量增加列表
    header: any;//新增、修改弹框名称
    // index: any;
    rowi: any;//修改行数
    ownerOrgInValue: any;//机构树inValue
    rateOrgName: any;//新增、修改弹框机构号对应的机构片区
    OrgName: any;//机构号对应的机构片区
    rateTermList: any = [];//期限码值
    rateTermLists: any = [];//新增、修改弹框期限码值
    baseRateInterestList: any = [];//利率单位码值
    is: any;//存储上一步页面是否可修改
    inType: any;//利率种类
    interestratemaintenance: InterestRateMaintenance = new InterestRateMaintenance();
    interestdefinedetail: InterestDefineDetail = new InterestDefineDetail();
    constructor(
        public httpService: ValuationHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) { }

    ngOnInit() {
        this.nextDate = new Date(this.minDate.getTime() + 24 * 60 * 60 * 1000);//	当前日期的后一天
        this.listOptions = [{ label: '请选择', value: '' }, { label: '基础利率', value: '1' }, { label: '基准利率', value: '3' }, { label: '市场利率', value: '4' }];//利率类别
        this.interestform = [{ label: '请选择', value: '' }, { label: '标准', value: 'bor' }, { label: 'libor', value: 'libor' }, { label: 'hibor', value: 'hibor' }, { label: 'sibor', value: 'sibor' }];//利率类型
        this.interestType = [{ label: '请选择', value: '' }, { label: '存款活期利率', value: '01' }, { label: '存款定期利率', value: '02' }, { label: '贷款利率', value: '03' }];//利率种类
        this.selectProductCondValue();
        this.codeVal();
        if (this.route.snapshot.params['data']) {
            this.data = JSON.parse(this.route.snapshot.params['data']);
            console.log(this.data);
            this.interestratemaintenance.rateName = this.data.baseRateName;
            this.interestdefinedetail.rateName = this.data.baseRateName;
            this.interestratemaintenance.rateDefineId = this.data.baseRateDefineId;
            this.interestdefinedetail.rateDefineId = this.data.baseRateDefineId;
            this.inType = this.data.baseRateSort;//利率种类
        }
        //工作流
        if (this.route.snapshot.params['baseRateDefineId'] && this.route.snapshot.params['baseRateName']) {
            //查询
            this.iswork = true;
            //修改页面数据初始化
            this.httpService.selectPriceRateDefine({ baseRateDefineId: this.route.snapshot.params['baseRateDefineId'], }).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.interestratemaintenance = data.productPriceRateDefine;
                }
            }, error => {
            })
            let params = {
            }
            if (this.route.snapshot.params['ratePurposeId']) {
                params = {
                    rateDefineId: this.route.snapshot.params['baseRateDefineId'],//	利率定义ID
                    ratePurposeId: this.route.snapshot.params['ratePurposeId'],//	利率ID
                    pageSize: this.pageSize,
                    pageNum: this.pageNum,
                }
            } else {
                params = {
                    rateDefineId: this.route.snapshot.params['baseRateDefineId'],//	利率定义ID
                    pageSize: this.pageSize,
                    pageNum: this.pageNum,
                }
            }
            this.httpService.selectPriceRatePurposelist(params).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.list_Table = data.returnList;
                    for (let l = 0; l < this.list_Table.length; l++) {
                        this.list_Table[l].fromDate = this.commfunc.transDateN(this.list_Table[l].fromDate);
                        this.list_Table[l].thruDate = this.commfunc.transDateN(this.list_Table[l].thruDate);
                        if (this.list_Table[l].rateOrgId && this.list_Table[l].rateOrgId != null) {
                            this.httpService.queryOrgById({ orgId: this.list_Table[l].rateOrgId }).subscribe(data => {//根据机构查询机构名
                                if (data.returnCode.code == 'success') {
                                    this.list_Table[l].rOrgName = data.orgName;
                                }
                                else {
                                    this.msgs = [];
                                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                                }
                            }, err => {
                                this.msgs = [];
                                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                            });
                        }
                    }
                    this.total = data.total;
                }
            }, err => {
            })
        }
        this.title = this.route.snapshot.params['title'];
        this.is = this.route.snapshot.params['is'];
        this.state = [{ label: '请选择', value: '' }, { label: '录入待复核', value: '0' }, { label: '复核生效', value: '1' }, { label: '修改待复核', value: '2' },
        { label: '作废待复核', value: '3' }, { label: '作废', value: '4' }, { label: '复核退回', value: '5' }];//状态
        this.baseRateInterestList = [{ label: '日利率', value: '01' }, { label: '月利率', value: '02' }, { label: '年利率', value: '03' }];
        if (this.title == '利率定义修改') {
            this.query();
        }
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //期限码值
    selectProductCondValue() {
        let params = {
            conditionalDimensionCode: 'baseRateTerm'
        }
        this.httpService.selectProductCondValueList(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let List = [];
                List.push({ "label": '请选择', "value": null });
                data.enumerationValueList.forEach((v) => {
                    List.push({ "label": v.dimensionValue, "value": v.dimensionCode });
                })
                this.rateTermList = List;
                this.rateTermLists = List.slice(1, List.length - 1);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '查询失败', detail: '调用服务失败！' });
        })
    }
    //新增、修改弹框机构树操作
    openOrg(param) {
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.display = false;
        if (this.header == '利率定义值新增') {
            if (!this.interestdefinedetail.rateOrgId) {
                this.showTree = !this.showTree;
            }
        } else if (this.header == '利率定义值修改') {
            this.ownerOrgInValue = 'adjustOrg';
        }
    }
    //新增、修改弹框机构弹框关闭
    onHideClose() {
        this.orgTreeDisplay = false;
        this.display = true;
    }
    //新增、修改弹框机构回调
    treeCall(param) {
        this.orgTreeDisplay = false;
        this.display = true;
        this.OrgNamenull = false;
        if (this.header == '利率定义值新增') {
            this.interestdefinedetail.rateOrgId = '';
            this.OrgName = ''
            param.forEach(item => {
                this.interestdefinedetail.rateOrgId += item.orgId + ",";
                this.OrgName += item.orgName + ",";
            })
            this.interestdefinedetail.rateOrgId = this.interestdefinedetail.rateOrgId.substring(0, this.interestdefinedetail.rateOrgId.length - 1);
            this.OrgName = this.OrgName.substring(0, this.OrgName.length - 1);
        } else if (this.header == '利率定义值修改') {
            this.interestdefinedetail.rateOrgId = param.orgId;
            this.OrgName = param.orgName;
        }

    }
    //机构树操作
    openOrgId(param) {
        this.orgTreeDisplay1 = true;
        this.ownerOrgInValue = 'adjustOrg'
    }
    //机构树弹框关闭
    onHideClose1() {
        this.orgTreeDisplay1 = false;
    }
    //机构树回调
    treeShow(param) {
        this.orgTreeDisplay1 = false;
        this.interestratemaintenance.rateOrgId = param.orgId;
        this.rateOrgName = param.orgName;
    }
    //生、失效日期大小比较
    selectTime(a) {
        if (!a) {
            this.fromDatenull = false;
        }
        this.interestdefinedetail.fromDate = this.commfunc.transDateN(this.interestdefinedetail.fromDate);
        this.interestdefinedetail.thruDate = this.commfunc.transDateN(this.interestdefinedetail.thruDate);
        if (this.interestdefinedetail.fromDate && this.interestdefinedetail.thruDate) {
            if (this.interestdefinedetail.fromDate >= this.interestdefinedetail.thruDate) {
                if (a) {
                    this.timeShow1 = true;
                    setTimeout(() => {
                        this.interestdefinedetail.thruDate = null;
                    })
                } else {
                    this.timeShow = true;
                    setTimeout(() => {
                        this.interestdefinedetail.fromDate = null;
                    })
                }
            } else {
                this.timeShow = false;
                this.timeShow1 = false;
            }
        }
    }
    //生、失效起止日期大小比较
    selectTime1(a) {
        if (this.fromDateBegin && this.fromDateEnd) {
            if (this.fromDateBegin >= this.fromDateEnd) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期起日不能大于生效日期止日' });
                if (a == 1) {
                    setTimeout(() => {
                        this.fromDateBegin = null;
                    })

                } else if (a == 2) {
                    setTimeout(() => {
                        this.fromDateEnd = null;
                    })
                }
            }
        }
        if (this.thruDateBegin && this.thruDateEnd) {
            if (this.thruDateBegin >= this.thruDateEnd) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '失效日期起日不能大于失效日期止日' });
                if (a == 3) {
                    setTimeout(() => {
                        this.thruDateBegin = null;
                    })

                } else if (a == 4) {
                    setTimeout(() => {
                        this.thruDateEnd = null;
                    })
                }
            }
        }
        if (this.fromDateBegin && this.thruDateBegin) {
            if (this.fromDateBegin >= this.thruDateBegin) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期起日不能大于失效日期起日' });
                if (a == 1) {
                    setTimeout(() => {
                        this.fromDateBegin = null;
                    })

                } else if (a == 3) {
                    setTimeout(() => {
                        this.thruDateBegin = null;
                    })
                }
            }
        }
        if (this.fromDateBegin && this.thruDateEnd) {
            if (this.fromDateBegin >= this.thruDateEnd) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '生效日期起日不能大于失效日期止日' });
                if (a == 1) {
                    setTimeout(() => {
                        this.fromDateBegin = null;
                    })

                } else if (a == 4) {
                    setTimeout(() => {
                        this.thruDateEnd = null;
                    })
                }
            }
        }
    }
    //查询
    query() {
        if (this.numberShow1) {
            return;
        }
        this.pageSize = 10;
        this.pageNum = 1;
        this.first = 0;
        this.selectPriceRatePurposelist();
    }
    selectPriceRatePurposelist() {
        let params = {
            rateDefineId: this.interestratemaintenance.rateDefineId,//	利率定义ID
            rateOrgId: this.interestratemaintenance.rateOrgId,//	机构号
            ratePurposeId: this.interestratemaintenance.ratePurposeId,//	利率ID
            rateTerm: this.interestratemaintenance.rateTerm,//	期限
            rateValue: this.interestratemaintenance.rateValue,//	利率值
            fromDateBegin: this.commfunc.transDateN(this.fromDateBegin),//	开始日期起
            fromDateEnd: this.commfunc.transDateN(this.fromDateEnd),//开始日期止
            thruDateBegin: this.commfunc.transDateN(this.thruDateBegin),//	结束日期起
            thruDateEnd: this.commfunc.transDateN(this.thruDateEnd),//	结束日期止
            rateStatus: this.interestratemaintenance.rateStatus,//	审批状态
            pageSize: this.pageSize,
            pageNum: this.pageNum,
        }
        this.httpService.selectPriceRatePurposelist(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list_Table = data.returnList;
                for (let l = 0; l < this.list_Table.length; l++) {
                    this.list_Table[l].fromDate = this.commfunc.transDateN(this.list_Table[l].fromDate);
                    this.list_Table[l].thruDate = this.commfunc.transDateN(this.list_Table[l].thruDate);
                    if (this.list_Table[l].rateOrgId && this.list_Table[l].rateOrgId != null) {
                        this.httpService.queryOrgById({ orgId: this.list_Table[l].rateOrgId }).subscribe(data => {//根据机构查询机构名
                            if (data.returnCode.code == 'success') {
                                this.list_Table[l].rOrgName = data.orgName;
                            }
                            else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                            }
                        }, err => {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                        });
                    }
                }
                this.total = data.total;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基准利率查询失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基准利率查询失败', detail: '调用服务失败！' });
        })
    }
    //翻页
    paginate(event) {
        this.pageSize = event.rows * 1;
        //当前页
        this.pageNum = event.page + 1;
        this.first = event.page * this.pageSize;
        this.selectPriceRatePurposelist();
    }
    //重置
    reset() {
        let name = this.interestratemaintenance.rateName;
        let DefineId = this.interestratemaintenance.rateDefineId;
        this.interestratemaintenance = new InterestRateMaintenance();
        this.rateOrgName = null;
        this.fromDateBegin = null;//	开始日期起
        this.fromDateEnd = null;//开始日期止
        this.thruDateBegin = null;//	结束日期起
        this.thruDateEnd = null;//	结束日期止
        this.interestratemaintenance.rateDefineId = DefineId;
        this.interestratemaintenance.rateName = name;
        this.numberShow1 = false;
        this.query();
    }
    // 数字校验
    onNumberCheck() {
        this.rateValueCodenull = false;
        if (this.interestdefinedetail.rateValueCode != '') {
            if (TwoInteger(null, this.interestdefinedetail.rateValueCode)) {
                this.numberShow = true;
            } else {
                this.numberShow = false;
            }
        } else {
            this.numberShow = false;
        }
    }
    onNumberCheck1() {
        if (this.interestratemaintenance.rateValue != '') {
            if (TwoInteger(null, this.interestratemaintenance.rateValue)) {
                this.numberShow1 = true;
            } else {
                this.numberShow1 = false;
            }
        } else {
            this.numberShow1 = false;
        }
    }

    //提交
    submit() {
        this.isClick = true;
        if (this.title == '利率定义新增') {
            this.createPriceRateDefine();
        } else if (this.title == '利率定义修改') {
            this.updatePriceRateDefine();
        }
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-maintenance', { query: '1' }]);
    }
    //存款基础利率新增
    createPriceRateDefine() {
        this.httpService.createPriceRateDefine(this.data).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '基础利率新增成功', detail: data.returnCode.message });
                // let List = this.list_Table;
                this.ratePurposeList = this.list_Table;
                // for (let i = List.length - 1; i >= 0; i--) {
                //     if (List[i].isQuoteFlage && List[i].isQuoteFlage == 1) {
                //         this.ratePurposeList.splice(i, 1);
                //     } else if (List[i].rateStatus && (List[i].rateStatus == 0 || List[i].rateStatus == 2 || List[i].rateStatus == 3 || List[i].rateStatus == 4)) {
                //         this.ratePurposeList.splice(i, 1);
                //     }
                // }
                if (this.ratePurposeList.length != 0) {
                    this.createPriceRatePurpose();
                    // this.updatePriceRatePurpose();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率新增失败', detail: data.returnCode.message });
                this.isClick = false;
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率新增失败', detail: '调用服务失败！' });
            this.isClick = false;
        })
    }
    //存款基础利率修改
    updatePriceRateDefine() {
        this.httpService.updatePriceRateDefine(this.data).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '基础利率修改成功', detail: data.returnCode.message });
                let List = this.list_Table;
                this.ratePurposeList = this.list_Table;
                for (let i = List.length - 1; i >= 0; i--) {
                    if (List[i].isQuoteFlage && List[i].isQuoteFlage == 1) {
                        this.ratePurposeList.splice(i, 1);
                    } else if (List[i].rateStatus && (List[i].rateStatus == 0 || List[i].rateStatus == 2 || List[i].rateStatus == 3 || List[i].rateStatus == 4)) {
                        this.ratePurposeList.splice(i, 1);
                    }
                }
                console.log(this.ratePurposeList);
                if (this.ratePurposeList.length != 0) {
                    // this.createPriceRatePurpose();
                    this.updatePriceRatePurpose();
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率修改失败', detail: data.returnCode.message });
                this.isClick = false;
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率修改失败', detail: '调用服务失败！' });
            this.isClick = false;
        })
    }
    //存款基础利率值新增
    createPriceRatePurpose() {
        let param = {
            ratePurposeList: this.ratePurposeList, //	批量增加
            // rateDefineId: this.interestratemaintenance.rateDefineId, //利率定义ID
            // rateValueCode: this.interestratemaintenance.rateValueCode, //	基准利率值
            // fromDate: this.commfunc.transDateN(this.interestratemaintenance.fromDate), //	生效日期
            // thruDate: this.commfunc.transDateN(this.interestratemaintenance.thruDate), //	失效日期
            // branchCode: this.interestratemaintenance.branchCode, //	分行代码
            // rateOrgId: this.interestratemaintenance.rateOrgId, //	机构号
            // rateTerm: this.interestratemaintenance.rateTerm, //期限
            // rateStatus: this.interestratemaintenance.rateStatus, //	利率状态
        }
        this.httpService.createPriceRatePurpose(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '基础利率值新增成功', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率值新增失败', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率值新增失败', detail: '调用服务失败！' });
        })
    }
    //存款基础利率值修改
    updatePriceRatePurpose() {
        let param = {
            ratePurposeList: this.ratePurposeList, //	批量增加
            // ratePurposeId: this.interestdefinedetail.ratePurposeId, //利率ID
            // rateValueCode: this.interestdefinedetail.rateValueCode, //	基准利率值
            // baseRateInterest: this.interestdefinedetail.baseRateInterest, //	利率单位
            // fromDate: this.commfunc.transDateN(this.interestdefinedetail.fromDate), //	生效日期
            // thruDate: this.commfunc.transDateN(this.interestdefinedetail.thruDate), //	失效日期
            // branchCode: this.interestdefinedetail.branchCode, //	分行代码
            // rateOrgId: this.interestdefinedetail.rateOrgId, //	机构号
            // rateTerm: this.interestdefinedetail.rateTerm, //期限
            // rateStatus: this.interestdefinedetail.rateStatus, //	利率状态
        }
        this.httpService.updatePriceRatePurpose(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '基础利率值修改成功', detail: data.returnCode.message });
                this.display = false;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '基础利率值修改失败', detail: data.returnCode.message });
                this.isClick = false;
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '基础利率值修改失败', detail: '调用服务失败！' });
            this.isClick = false;
        })
    }
    //上一步
    back() {
        let backdata = JSON.stringify(this.data);
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-mainadd', { title: this.title, is: this.is, backdata:backdata }])
    }
    return() {
        this.router.navigate(['/pages/tzb/valuation/interest-rate/interest-rate-maintenance', { query: '1' }])
    }
    //新增
    addBtn() {
        this.display = true;
        this.numberShow = false;
        this.timeShow = false;
        this.timeShow1 = false;
        this.OrgNamenull = false;
        this.fromDatenull = false;
        this.rateValueCodenull = false;
        this.header = '利率定义值新增';
        let rateName = this.interestdefinedetail.rateName;
        let rateDefineId = this.interestdefinedetail.rateDefineId;
        this.interestdefinedetail = new InterestDefineDetail();
        this.OrgName = '';
        if(this.inType != '01'){
            this.interestdefinedetail.rateTerm = '201';
        }
        this.interestdefinedetail.baseRateInterest = '01';
        this.interestdefinedetail.rateName = rateName;
        this.interestdefinedetail.rateDefineId = rateDefineId;
        this.interestdefinedetail.rateOrgId = '';
        this.interestdefinedetail.fromDate = this.commfunc.transDateN(this.minDate);
    }
    //修改
    toUpdate(i, col) {
        this.display = true;
        this.numberShow = false;
        this.timeShow = false;
        this.timeShow1 = false;
        this.OrgNamenull = false;
        this.fromDatenull = false;
        this.rateValueCodenull = false;
        this.OrgName = '';
        this.header = '利率定义值修改';
        this.rowi = i;
        let rateName = this.interestdefinedetail.rateName;
        let rateDefineId = this.interestdefinedetail.rateDefineId;
        this.interestdefinedetail = _.clone(col);
        this.interestdefinedetail.rateName = rateName;
        this.interestdefinedetail.rateDefineId = rateDefineId;
        // if (col.ratePurposeId && col.ratePurposeId != null) {
        //     this.index = '1';
        // } else {
        //     this.index = '';
        // }
        if (this.interestdefinedetail.rateOrgId && this.interestdefinedetail.rateOrgId != null) {
            this.httpService.queryOrgById({ orgId: this.interestdefinedetail.rateOrgId }).subscribe(data => {//根据机构号查询机构名
                if (data.returnCode.code == 'success') {
                    this.OrgName = data.orgName;
                }
                else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }
    //详情
    toDetail(col) {
        this.OrgNamenull = false;
        this.fromDatenull = false;
        this.rateValueCodenull = false;
        this.numberShow = false;
        this.timeShow = false;
        this.timeShow1 = false;
        this.display = true;
        this.OrgName = '';
        this.header = '利率定义值详情';
        let rateName = this.interestdefinedetail.rateName;
        let rateDefineId = this.interestdefinedetail.rateDefineId;
        this.interestdefinedetail = col;
        this.interestdefinedetail.rateName = rateName;
        this.interestdefinedetail.rateDefineId = rateDefineId;
        if (this.interestdefinedetail.rateOrgId && this.interestdefinedetail.rateOrgId != null) {
            this.httpService.queryOrgById({ orgId: this.interestdefinedetail.rateOrgId }).subscribe(data => {//根据机构号查询机构名
                if (data.returnCode.code == 'success') {
                    this.OrgName = data.orgName;
                }
                else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
            });
        }
    }
    cancel() {
        this.display = false;
        console.log(this.list_Table);
    }
    //确认
    save() {
        //机构号不能为空
        if (!this.OrgName) {
            this.OrgNamenull = true;
            return;
        }
        //利率值不能为空
        if (!this.interestdefinedetail.rateValueCode) {
            this.rateValueCodenull = true;
            return;
        }
        //生效日期不能为空
        if (!this.interestdefinedetail.fromDate) {
            this.fromDatenull = true;
            return;
        }
        if (this.numberShow) {
            return;
        }
        if (this.header == '利率定义值新增') {
            let aa = _.clone(this.list_Table);
            this.interestdefinedetail.fromDate = this.commfunc.transDateN(this.interestdefinedetail.fromDate);//	生效日期
            if (this.interestdefinedetail.thruDate == '' || this.interestdefinedetail.thruDate == null) {
                this.interestdefinedetail.thruDate = '9999-12-31';
            } else {
                this.interestdefinedetail.thruDate = this.commfunc.transDateN(this.interestdefinedetail.thruDate);
            }
            for (let i = 0; i <= this.interestdefinedetail.rateOrgId.split(",").length - 1; i++) {
                aa.push({
                    rateDefineId: this.interestdefinedetail.rateDefineId, rateName: this.interestdefinedetail.rateName,
                    rateOrgId: this.interestdefinedetail.rateOrgId.split(",")[i], ratePurposeId: this.interestdefinedetail.ratePurposeId, rateTerm: this.interestdefinedetail.rateTerm,
                    rateValueCode: this.interestdefinedetail.rateValueCode, fromDate: this.interestdefinedetail.fromDate, thruDate: this.interestdefinedetail.thruDate,
                    baseRateInterest: this.interestdefinedetail.baseRateInterest
                });
                this.total++;
            }
            this.list_Table = aa;
            this.display = false;
            for (let l = 0; l < this.list_Table.length; l++) {
                if (this.list_Table[l].rateOrgId && this.list_Table[l].rateOrgId != null) {
                    this.httpService.queryOrgById({ orgId: this.list_Table[l].rateOrgId }).subscribe(data => {//根据机构查询机构名
                        if (data.returnCode.code == 'success') {
                            this.list_Table[l].rOrgName = data.orgName;
                        }
                        else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                    });
                }
            }
        } else if (this.header == '利率定义值修改') {
            // if (this.index) {
            //     this.updatePriceRatePurpose();
            // }
            this.interestdefinedetail.fromDate = this.commfunc.transDateN(this.interestdefinedetail.fromDate);//	生效日期
            if (this.interestdefinedetail.thruDate == '' || this.interestdefinedetail.thruDate == null) {
                this.interestdefinedetail.thruDate = '9999-12-31';
            } else {
                this.interestdefinedetail.thruDate = this.commfunc.transDateN(this.interestdefinedetail.thruDate);
            }
            let aa = _.clone(this.list_Table);
            let bb = [];
            bb.push({
                rateDefineId: this.interestdefinedetail.rateDefineId, rateName: this.interestdefinedetail.rateName,
                rateOrgId: this.interestdefinedetail.rateOrgId, ratePurposeId: this.interestdefinedetail.ratePurposeId, rateTerm: this.interestdefinedetail.rateTerm,
                rateValueCode: this.interestdefinedetail.rateValueCode, fromDate: this.interestdefinedetail.fromDate, thruDate: this.interestdefinedetail.thruDate,
                baseRateInterest: this.interestdefinedetail.baseRateInterest
            });
            aa[this.rowi] = bb[0];
            this.list_Table = aa;
            this.display = false;
            for (let l = 0; l < this.list_Table.length; l++) {
                if (this.list_Table[l].rateOrgId && this.list_Table[l].rateOrgId != null) {
                    this.httpService.queryOrgById({ orgId: this.list_Table[l].rateOrgId }).subscribe(data => {//根据机构查询机构名
                        if (data.returnCode.code == 'success') {
                            this.list_Table[l].rOrgName = data.orgName;
                        }
                        else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: 'Error Message', detail: data.returnCode.message });
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用失败！' }];
                    });
                }
            }
        }

    }
    //删除
    toDelete(i, col) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                if (col.ratePurposeId && col.ratePurposeId != null) {
                    //条件
                    let param = {
                        ratePurposeId: col.ratePurposeId,//利率ID
                        baseRateDefineId: col.rateDefineId,//利率编号
                        baseRateName: col.rateName,//利率名称
                    };
                    this.httpService.removePriceRatePurpose(param).subscribe(data => {
                        if (data.returnCode.code == "success") {
                            this.msgs = [];
                            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                            col.rateStatus = '3';
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                    })
                } else {
                    this.list_Table.splice(i, 1);
                    this.list_Table = _.clone(this.list_Table);
                    this.total--;
                }
            }
        });
    }
}
